import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [{
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/staking",
    name: "Staking",
    component: () => import("../views/Staking.vue")
  },
  {
    path: "/dashboard",
    name: "Dashboard",
    component: () => import("../components/staking/stakeone.vue")
  }
];

const router = new VueRouter({
  routes,
});

export default router;